import React, {useState} from 'react';
import {data} from "./data";

const Timeline = () => {
    const [min, setMin] = useState(true)

    let handleClickToMinimize = () => {
        setMin(true)
    }
    let handleClickToExpand = () => {
        setMin(false)
    }

    let yearsOfWorkArray = () => {
        let years = []

        let firstYear = data[0].startPoint.year

        for (let i = currentDate.getFullYear(); i >= firstYear; i--) {
            years.unshift(i)
        }

        return years
    }

    const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"]

    let years = () => {
        let yearItems = []

        yearsOfWorkArray().forEach((item)=>{
            yearItems.push(
                <div className="about__timeline__year">
                    <div className="about__timeline__months">
                        {generateMonths(item)}
                    </div>
                    <h3 className="about__timeline__year-title">{item}</h3>
                </div>
            )
        })

        return yearItems
    }

    function getMonthDifference(startDate, endDate) {
        return (
            endDate.getMonth() -
            startDate.getMonth() +
            12 * (endDate.getFullYear() - startDate.getFullYear())
        );
    }

    let currentDate = new Date()

    let currentYear = currentDate.getFullYear()
    let currentMonth = currentDate.getMonth() + 1
    let currentDay = currentDate.getDate()

    let currentDateStr = `${currentYear}-${currentMonth}-${currentDay}`


    let generateMonths = (year) => {
        let yearStr = year.toString().slice(-2)
        let monthsArr = []

        let yearWithJob = false

        data.forEach((job, id) => {

            if (job.startPoint.year === year) {

                let jobMonthsDuration = 0

                if (job.endPoint) {
                    jobMonthsDuration = getMonthDifference(new Date(job.startPoint.str), new Date(job.endPoint.str))
                } else {
                    jobMonthsDuration = getMonthDifference(new Date(job.startPoint.str), new Date(currentDateStr))
                }


                yearWithJob = true

                let startMonth

                id ? startMonth = 1 : startMonth = job.startPoint.month;

                let isCurrentJob = id + 1 === data.length

                for (let i = startMonth; i <= 12; i++){

                    if (i === job.startPoint.month){
                        monthsArr.push(
                            <div className={`about__timeline__months__item ${isCurrentJob && "work_start"}`}>
                                <h3 className="about__timeline__work">
                                    {/*<span className="about__timeline__work-title">Sbitsoft Web Development</span>*/}
                                    <span className="about__timeline__work-title">{job.title}
                                        {isCurrentJob && <span className="only_min about__timeline__work-currently"> - Currently</span>}
                                    </span>
                                    <span className="about__timeline__work-line"
                                          style={{height: `calc(${monthItemHeight}px*${jobMonthsDuration} - 20px)`}}
                                    >
                                    <span className={`about__timeline__work-line--inner ${isCurrentJob && "current"}`}></span>
                                    </span>
                                </h3>
                                <span><span>{months[i-1]}</span><span className="only_min"> /{yearStr}</span></span>
                            </div>
                        )
                    } else if(i === job.startPoint.month + 1){
                        monthsArr.push(
                            <div className="about__timeline__months__item">
                                <div className='about__timeline__months__item-position'>as {job.position}</div>
                                <span><span>{months[i-1]}</span><span className="only_min"> /{yearStr}</span></span>
                            </div>
                        )
                    } else {
                        monthsArr.push(
                            <div className="about__timeline__months__item">
                                <span><span>{months[i-1]}</span><span className="only_min"> /{yearStr}</span></span>
                            </div>
                        )
                    }


                }

            }

        })

        if (!yearWithJob) {
            let lastMonth = 1

            if(year === currentYear){
                lastMonth = currentMonth
            } else lastMonth = 12


            for (let i = 1; i <= lastMonth; i++){
                monthsArr.push(
                    <div className="about__timeline__months__item">
                        <span><span>{months[i-1]}</span><span className="only_min"> /{yearStr}</span></span>
                    </div>
                )
            }
        }


        return monthsArr

    }


    const [monthItemHeight, setMonthItemHeight] = useState(60)

    let onSetMonthItemHeight = () => {
        if (window.innerWidth <= 768){
            setMonthItemHeight(40)
        } else{
            setMonthItemHeight(60)
        }
    }

    window.addEventListener('resize', onSetMonthItemHeight)

    return (
        <div className={`about__timeline${min ? " minimized" : ""}`} onClick={min ? handleClickToExpand : undefined}>
            <div className="about__timeline-inner">
                <button className="about__timeline-btn" onClick={handleClickToMinimize}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M0 3.795l2.995-2.98 11.132 11.185-11.132 11.186-2.995-2.981 8.167-8.205-8.167-8.205zm18.04 8.205l-8.167 8.205 2.995 2.98 11.132-11.185-11.132-11.186-2.995 2.98 8.167 8.206z"/></svg>
                </button>

                {years()}

            </div>
        </div>
    );
}

export default Timeline;